import { faThumbsDown, faThumbsUp } from '@fortawesome/free-regular-svg-icons';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import SaveVideo from '../save_video/save_video';
import styles from './video_detail.module.css';

const VideoDetail = ({ selectedVideo, selectedVideo: { video, channel } }) => {
  const [saveVideoOpen, setSaveVideoOpen] = useState(false);
  const user = useSelector(state => state.authService.user);
  const {
    repository: videoRepo,
    feeds: { likeList },
  } = useSelector(state => state.videoRepository);

  const toggleSaveVideo = () => {
    setSaveVideoOpen(!saveVideoOpen);
  };

  const closePopup = e => {
    e.preventDefault();
    setSaveVideoOpen(false);
  };

  const compCount = count => {
    const num = Number(count);
    if (count > 1000) {
      const div = count / 10000;
      const transNum =
        div > 100 ? parseInt(div) : parseFloat(String(div).slice(0, 5));
      return `${transNum.toLocaleString('en-IN')}만`;
    } else {
      return `${num}`;
    }
  };

  const clickLike = () => {
    console.log('click like');
    const videos = likeList || [];
    if (user) {
      videoRepo.saveLike(user.uid, [selectedVideo, ...videos]);
    }
  };

  return (
    <div className={styles.detail}>
      <div className={styles.videoBox}>
        <iframe
          className={styles.iframe}
          id={video.id}
          title={video.snippet.title}
          width="100%"
          height="100%"
          src={`https://www.youtube.com/embed/${video.id}`}
          frameBorder="0"
        ></iframe>
      </div>
      <div className={styles.videoInfo}>
        <div className={styles.infoTop}>
          {video.snippet.tags &&
            video.snippet.tags.slice(0, 3).map(tag => (
              <span className={styles.tag} key={tag}>
                #{tag}
              </span>
            ))}
          <h3 className={styles.videoTitle}>{video.snippet.title}</h3>
          <div className={styles.videoPrimary}>
            <p className={styles.videoDetails}>
              {video.statistics && video.statistics.viewCount && (
                <>
                  <span className={styles.viewCount}>
                    조회수{' '}
                    {Number(video.statistics.viewCount).toLocaleString('en-US')}
                    회
                  </span>
                  <span> • </span>
                </>
              )}
              <span className={styles.published}>
                {new Date(video.snippet.publishedAt) //
                  .toLocaleDateString('ko-KR')}
              </span>
            </p>
            <div className={styles.rightBox}>
              <button className={styles.like} onClick={clickLike}>
                <FontAwesomeIcon icon={faThumbsUp} className={styles.icon} />
                <span className={styles.txt}>좋아요</span>
              </button>
              <button className={styles.dislike}>
                <FontAwesomeIcon icon={faThumbsDown} className={styles.icon} />
                <span className={styles.txt}>싫어요</span>
              </button>
              <button className={styles.saveVideoBtn} onClick={toggleSaveVideo}>
                <FontAwesomeIcon icon={faPlus} className={styles.icon} />
                <span className={styles.txt}>저장</span>
              </button>
              {saveVideoOpen && <SaveVideo closePopup={closePopup} />}
            </div>
          </div>
        </div>
        <div className={styles.infoDetails}>
          <div className={styles.channelBox}>
            <div className={styles.logoBox}>
              {channel && channel.snippet.thumbnails && (
                <img
                  className={styles.logo}
                  src={channel.snippet.thumbnails.default.url}
                  alt="channel logo"
                />
              )}
            </div>
            <div className={styles.channel}>
              <h4 className={styles.channel}>{video.snippet.channelTitle}</h4>
              {channel && channel.statistics.subscriberCount && (
                <p className={styles.subscriber}>
                  구독자 {compCount(channel.statistics.subscriberCount)}명
                </p>
              )}
            </div>
          </div>
          <div className={styles.tags}>
            {video.snippet.tags &&
              video.snippet.tags.map(tag => (
                <span className={styles.tag} key={tag}>
                  #{tag}
                </span>
              ))}
          </div>
          <pre className={styles.desc}>{video.snippet.description}</pre>
        </div>
      </div>
    </div>
  );
};

export default VideoDetail;
